            loader.load('static/images/deviceYg.png',function(texture){
                let smokegeometry = new THREE.BoxGeometry(50, 50, 2);
                let smokematerial = new THREE.MeshBasicMaterial({map:texture});
                smokematerial.opacity=1.0;
                smokematerial.transparent=true;
                let smoke = new THREE.Mesh( smokegeometry,smokematerial);
                smoke.position.set(0,-30,280);
                smoke.rotation.x += Math.PI; 
                smoke.name = 'smoke';
                let smoke1=smoke.clone();
                smoke1.position.set(480,60,200);
                smoke1.name = 'smoke1';
                _this.scene.add(smoke);
                _this.scene.add(smoke1);
            });
        },

        removeSmoke(){
            let _this = this;
            let allChildren = this.scene.children;
            for (let i=3;i<allChildren.length;i++){
                if (allChildren[i].name.slice(0,5) == 'smoke'){
                    this.scene.remove(allChildren[i--]);
                }
            }
        },

