        ray(event){
            let Sx = event.clientX;
            let Sy = event.clientY;
            let x = ( Sx / window.innerWidth ) * 2 - 1;
            let y = -( Sy / window.innerHeight ) * 2 + 1;
            let standardVector  = new THREE.Vector3(x, y, 0.5);
            let worldVector = standardVector.unproject(this.camera);
            let ray = worldVector.sub(this.camera.position).normalize();
            let raycaster = new THREE.Raycaster(this.camera.position, ray);
            let objChildren = this.scene.children;
            let intersects = raycaster.intersectObjects(objChildren);
            if (intersects.length > 0) {
                let meshNumber = intersects[0].object.ID;
                let meshName = intersects[0].object.name;
                console.log(meshName);
                if(meshName.indexOf('house')>=0){
                    this.messageHouseNum = meshName.slice(5);
                    getTotalHouse(10022).then(response => {
                        let res = response.data.data;
                        for(let item of res){
                            if(item.buildingName == this.messageHouseNum+''){
                                if(item.id == '' || item.id == null){
                                    this.messageHouseId = 0;
                                }else{
                                    this.messageHouseId = item.id;
                                }
                            }
                        }
                    });
                    this.openDialogHouseDetailThree()
                }
            }

        },

        ray2(event){
            let Sx = event.clientX;
            let Sy = event.clientY;
            let x = ( Sx / window.innerWidth ) * 2 - 1;
            let y = -( Sy / window.innerHeight ) * 2 + 1;
            let standardVector  = new THREE.Vector3(x, y, 0.5);
            let worldVector = standardVector.unproject(this.camera);
            let ray = worldVector.sub(this.camera.position).normalize();
            let raycaster = new THREE.Raycaster(this.camera.position, ray);
            let objChildren = this.scene.children;
            let intersects = raycaster.intersectObjects(objChildren);
            if (intersects.length > 0) {
                let meshName = intersects[0].object.name;
                let meshNo = intersects[0].object.ID;
                if(meshName.indexOf('smoke')>=0){
                    this.messageSmokeNum = meshName.slice(5);
                    this.messageDeviceNo = meshNo;
                    this.$refs.childsmokeTable.getList(this.messageDeviceNo);
                    this.opener_tableMoke();
                }
                if(meshName.indexOf('guard')>=0){
                    this.messageGuardNum = meshName.slice(5);
                    this.messageDeviceNo = meshNo;
                    this.$refs.childmjTable.getList(this.messageDeviceNo);
                    this.opener_tableMj();
                }
                if(meshName.indexOf('peopl')>=0){
                    this.messageMonitorNum = meshName.slice(5);
                    this.messageMonitorIP = meshNo;
                    // this.messagePeopleNum = meshName.slice(5);
                    // this.messageDeviceNo = meshNo;
                    // this.$refs.childpeopleTable.getList(this.messageDeviceNo);
                    // this.opener_tablePeople();
                    this.handleCreateSp();
                }
                // if(meshName.indexOf('peopl')>=0){
                //     this.messagePeopleNum = meshName.slice(5);
                //     this.messageDeviceNo = meshNo;
                //     this.$refs.childpeopleTable.getList(this.messageDeviceNo);
                //     this.opener_tablePeople();
                // }
                if(meshName.indexOf('pgate')>=0){
                    this.messagePeoplegateNum = meshName.slice(5);
                    this.messageDeviceNo = meshNo;
                    this.$refs.childpeoplegateTable.getList(this.messageDeviceNo);
                    this.opener_tablePeoplegate();
                }
                if(meshName.indexOf('dgate')>=0){
                    this.messageDdcgateNum = meshName.slice(5);
                    this.messageDeviceNo = meshNo;
                    this.$refs.childddcgateTable.getList(this.messageDeviceNo);
                    this.opener_tableDdcgate();
                }
                if(meshName.indexOf('cgate')>=0){
                    this.messageClgateNum = meshName.slice(5);
                    this.messageDeviceNo = meshNo;
                    this.$refs.childclgateTable.getList(this.messageDeviceNo);
                    this.opener_tableClgate();
                }
                if(meshName.indexOf('cdzwl')>=0){
                    this.messageDzwlgateNum = meshName.slice(5);
                    this.messageDeviceNo = meshNo;
                    this.$refs.childdzwlgateTable.getList(this.messageDeviceNo);
                    this.opener_tableDzwlgate();
                }
                if(meshName.indexOf('cwifi')>=0){
                    this.$message({
                        message: 'WIFI',
                        type: 'warning'
                    });
                }
            }
        },

