        //
        createResultBsp(name,bsp,less_bsp,mat){
            let material
            switch(mat)
            {
                case 1:
                    material = new THREE.MeshPhongMaterial({color:0x9cb2d1,specular:0x9cb2d1,shininess:30,transparent:true,opacity:1});
                    break;
                case 2:
                    material = new THREE.MeshPhongMaterial({color:0xafc0ca,specular:0xafc0ca,shininess:30,transparent:true,opacity:1});
                    break;
                default:
            }

            let sphere1BSP = new ThreeBSP(bsp);
            let cube2BSP = new ThreeBSP(less_bsp);//0x9cb2d1 ,0xC3C3C3  , 0xafc0ca
            let resultBSP = sphere1BSP.subtract(cube2BSP);
            let result = resultBSP.toMesh(material);
            result.material.flatshading = THREE.FlatShading;
            result.geometry.computeFaceNormals();  //
            result.geometry.computeVertexNormals();
            result.material.needsUpdate = true;  //
            result.geometry.buffersNeedUpdate = true;
            result.geometry.uvsNeedUpdate = true;
            result.name = name;
            this.scene.add(result);
        },

        //
        createResultBspAdd(number,name,bsp,less_bsp,mat,angle){
            let material
            switch(mat)
            {
                case 1:
                    material = new THREE.MeshPhongMaterial({color:0x0783B0,specular:0x9cb2d1,shininess:30,transparent:true,opacity:1});
                    break;
                case 2:
                    material = new THREE.MeshPhongMaterial({color:0x9cb2d1,specular:0xafc0ca,shininess:30,transparent:true,opacity:1});
