        buildMovingCars() {
            let carsPosition = [
                [-650, 725, 0],
                [150, 725, 0],
                [925, 100, 2.5],
                [150, -725, 5],
                [-925, -300, 7.5]
            ]
            carsPosition.forEach(elem => {
                let car = new Car()
                let x = elem[0],
                    z = elem[1],
                    r = elem[2]
                car.setPosition(x, -200, z)
                car.mesh.rotation.y = r * Math.PI
                this.cars.push(car)
                this.scene.add(car.mesh)
            })
        },

