    background: red;
}
.arrow_cont {
    width: 70px;
    height: 124px;
    float: left;
}
.history_btn {
    height: 55px;
    margin-bottom: 10px;
    background: rgba(0,21,40,0.5);
    border-radius: 4px;
    text-align: center;
    padding-top: 5px;
    cursor: pointer;
}
.history_btn img {
    width: 58px;
}
.arrow_btn {
    border-radius: 4px;
    height: 55px;
    color: #fff;
    text-align: center;
    vertical-align: middle;
    font-size: 30px;
    background: rgba(0,21,40,0.5);
}
.arrow_btn img {
    width: 15px;
}
.arrow_btn:hover {
    background-color: rgba(70,89,126,0.8);
}
.layer_cont {
    text-align: center;
    // float: right;
    width: 930px;
    height: 80px;
    background: url("../../assets/images/line.png") bottom no-repeat;
    background-size: 100%;
    // border-bottom: 2px solid #07b2f5;
    // background: rgba(0,21,40,0.5);
    border-bottom-left-radius:4px;
    border-bottom-right-radius:4px;
}
.a{
    background: black;
    text-align: center;
}
.topCont{
    width: 870px;
    height: 80px;
    margin: 0 auto;
    background: yellow;
}
.titleTextCont{
    width: 400px;
    height: 50px;
    color: #ffffff;
    font-size: 20px;
    position: absolute;
    top: 140px;
    left: 50%;
    margin-left: -200px;
    text-align: center;
    line-height: 50px;
    background: url("../../assets/images/titleText.png") center no-repeat;
    background-size: 100% 100%;
}
.btnCont{
    color: #ffffff;
    width: 50px;
    height: 20px;
    position: absolute;
    left: 50%;
    text-align: center;
    margin-left: -25px;
    background: url("../../assets/images/btnIcon.png") center no-repeat;
    background-size: 100% 100%;
    
}
.layer_cont ul {
    width: 100%;
    height: 100%;
}
.layer_cont ul li {
    // width: 100px;
    height: 100%;
    text-align: center;
    float: left;
    padding-top: 50px;
    box-sizing: border-box;
    position: relative;
    margin: 0 15px;
}
.layer_cont ul li a{
    color: #fff;
}
.select{
    color: #3fefff !important;
    font-weight: bold;
}
.layer_cont ul li span {
    display: block;
    width: 1px;
    height: 30px;
    background-color: rgba(255,255,255,0.4);
    position: absolute;
    right: 0;
    top: 40px;
}
.layer_cont .det_tit_curspan {
    background-color: rgba(255,255,255,0.3);
}
.layer_cont .det_tit_curspan2 a {
    color: #3fefff !important;
    font-weight: bold;
}

.layer_cont .layer_cont ul li a {
    display: block;
    width: 68px;
}
.layer_cont .img_sty {
    width: auto;
    height: 48px;
    opacity:0.5;
    filter:alpha(opacity=50);
}

.layer_cont .det_tit_curspan img {
    opacity:1;
    filter:alpha(opacity=100);
}
.layer_cont .det_tit_curspan2 img {
    opacity:1;
    filter:alpha(opacity=100);
}
.layer_cont .det_tit_curspanImg {
    opacity:1;
    filter:alpha(opacity=100);
}
.layer_cont .det_tit_curspanImg2 {
    opacity:1;
    filter:alpha(opacity=100);
}

.routate_sty i{
    display: inline-block;
    transform:rotate(180deg);
    -ms-transform:rotate(180deg); 	/* IE 9 */
    -moz-transform:rotate(180deg); 	/* Firefox */
    -webkit-transform:rotate(180deg); /* Safari  Chrome */
    -o-transform:rotate(180deg); 	/* Opera */
}
.frameCont{
    width:50%;
    height:50%;
    border: 1px solid red;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 12;

}
.iconfont {
    font-size: 18px;
}

</style>



