        getIntersects(event) {
            event.preventDefault();
            this.mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
            this.mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;
            this.raycaster.setFromCamera(this.mouse, this.camera);
            let intersects = this.raycaster.intersectObjects(this.scene.children);
            return intersects;
        },

        // 
        changeMaterial(object) {
            let material = new THREE.MeshLambertMaterial({
                // color: 0xffffff * Math.random(),
                color: 0x58ACFA,
                transparent: object.material.transparent ? false : true
                // opacity: 0.8
            });
            object.material = material;
        },


        ray(event){
            let Sx = event.clientX;
            let Sy = event.clientY;
            let x = ( Sx / window.innerWidth ) * 2 - 1;
            let y = -( Sy / window.innerHeight ) * 2 + 1;
            let standardVector  = new THREE.Vector3(x, y, 0.5);
            let worldVector = standardVector.unproject(this.camera);
            let ray = worldVector.sub(this.camera.position).normalize();
            let raycaster = new THREE.Raycaster(this.camera.position, ray);
            let objChildren = this.scene.children;
            let intersects = raycaster.intersectObjects(objChildren);
            if (intersects.length > 0) {
                let meshName = intersects[0].object.name;
            }

        },

