        createCubeWall(name,width, height, depth,angle,material,x,y,z){
            let cubeGeometry = new THREE.BoxGeometry(width, height, depth );
            let cube = new THREE.Mesh( cubeGeometry, material );
            cube.position.x = x;
            cube.position.y = y;
            cube.position.z = z;
            cube.rotation.y += angle*Math.PI;  
            cube.name = name;
            this.scene.add(cube);
        },

        //
        createHouseWall(name,width, height, depth,angle,x,y,z){
            let cubeGeometry = new THREE.BoxGeometry(width, height, depth );
            let material = new THREE.MeshPhongMaterial({
                color:0x0783B0,
                specular:0x9cb2d1,
                shininess:30,
                transparent:true,
                opacity:1
            });
            let cube = new THREE.Mesh( cubeGeometry, material );
            cube.position.x = x;
            cube.position.y = y;
            cube.position.z = z;
            cube.rotation.y += angle*Math.PI;  
            cube.name = name;
            this.scene.add(cube);
        },

    
        //
        createOutHouseWall(name,width, height, depth,angle,x,y,z){
            let cubeGeometry = new THREE.BoxGeometry(width, height, depth );
            let material = new THREE.MeshPhongMaterial({
                color: 0xafc0ca,
                specular:0x9cb2d1,
                shininess:30,
                transparent:true,
                opacity: 0.3
            });
            let cube = new THREE.Mesh( cubeGeometry, material );
            cube.position.x = x;
            cube.position.y = y;
            cube.position.z = z;
            cube.rotation.y += angle*Math.PI;  
            cube.name = name;
            this.scene.add(cube);
        },

        createGroupobj(name,width, height, depth,angle,x,y,z){
            let cubeGeometry = new THREE.BoxGeometry(width, height, depth );
            let material = new THREE.MeshBasicMaterial({
                color: Math.random() * 0xffffff,
                opacity: 0.5
            });
            let cube = new THREE.Mesh( cubeGeometry, material );
            cube.position.x = x;
            cube.position.y = y;
            cube.position.z = z;
            cube.rotation.y += angle*Math.PI;  
            cube.name = name;
            this.house.add(cube);
        },


        //
        returnWallObject(width, height, depth,angle,material,x,y,z){
            let cubeGeometry = new THREE.BoxGeometry(width, height, depth);
            // let material = new THREE.MeshPhongMaterial({
            //     color: 0xafc0ca
            //     // opacity: 0.5
            // });
            let cube = new THREE.Mesh( cubeGeometry, material );
            cube.position.x = x;
            cube.position.y = y;
            cube.position.z = z;
            cube.rotation.y += angle*Math.PI;
            return cube;
        },

        //
        returnCy(top, bottom, height,j,w,material,x,y,z){
            let cubeGeometry = new THREE.CylinderGeometry(top, bottom, height);
            // let material = new THREE.MeshPhongMaterial({
            //     color: 0xafc0ca
            //     // opacity: 0.5
            // });
            let cube = new THREE.Mesh( cubeGeometry, material );
            cube.position.x = x;
            cube.position.y = y;
            cube.position.z = z;
            return cube;
        },

        //
        createWallMaterail(){
            this.matArrayA.push(new THREE.MeshPhongMaterial({color: 0xafc0ca}));  
            this.matArrayA.push(new THREE.MeshPhongMaterial({color: 0xafc0ca}));  
            this.matArrayA.push(new THREE.MeshPhongMaterial({color: 0xd6e4ec}));  
            this.matArrayA.push(new THREE.MeshPhongMaterial({color: 0xd6e4ec}));  
            this.matArrayA.push(new THREE.MeshPhongMaterial({color: 0xafc0ca}));  
            this.matArrayA.push(new THREE.MeshPhongMaterial({color: 0xafc0ca}));  

            this.matArrayB.push(new THREE.MeshPhongMaterial({color: 0xafc0ca}));  
            this.matArrayB.push(new THREE.MeshPhongMaterial({color: 0xafc0ca})); 
            this.matArrayB.push(new THREE.MeshPhongMaterial({color: 0xd6e4ec})); 
            this.matArrayB.push(new THREE.MeshPhongMaterial({color: 0xafc0ca}));  
            this.matArrayB.push(new THREE.MeshPhongMaterial({color: 0xafc0ca}));  
            this.matArrayB.push(new THREE.MeshPhongMaterial({color: 0xafc0ca}));  

        },
        
        createLayout(){
            if(this.messageHouseNum == '5' || this.messageHouseNum == '9'){
                let wallB1=this.returnWallObject(200, 260,40,0,this.matArrayB,0,110,0);
                let wallB1_1=this.returnWallObject(80, 260,40,0,this.matArrayB,0,110,40);
                this.createResultBspAdd(this.messageHouseNum,wallB1,wallB1_1,1,0.2);
            }else if(this.messageHouseNum == '7' || this.messageHouseNum == '8' || this.messageHouseNum == '11' || this.messageHouseNum == '12' || this.messageHouseNum == '13' || this.messageHouseNum == '14' || this.messageHouseNum == '15' || this.messageHouseNum == '16' || this.messageHouseNum == '18' || this.messageHouseNum == '4'){
                let wall2=this.returnWallObject(250, 260,40,0,this.matArrayB,-5,110,0);
                let wall2_1=this.returnWallObject(60, 260,40,0,this.matArrayB,-60,110,40);
                let wall2_2=this.returnWallObject(60, 260,40,0,this.matArrayB,50,110,40);
                let house2One = this.createResultBspReturn(wall2,wall2_1,1);
                this.createResultBspAdd(this.messageHouseNum,house2One,wall2_2,1,0);
            }else{
                this.createHouseWall(this.messageHouseNum,200, 260, 100,0, 0,110,0);
            }

        },

        createSpriteText2(text,x,y,z){
            let canvas = document.createElement('canvas');
            canvas.width=256;
            canvas.height=128;
            let ctx = canvas.getContext('2d');
            ctx.fillStyle = '#bbf42f';
            ctx.font = 'Bold 80px Arial';
            ctx.lineWidth = 4;
            ctx.fillText(text,4,104);
            let texture = new THREE.Texture(canvas);
            texture.needsUpdate = true;

            let material = new THREE.SpriteMaterial({map:texture});
            let textObj = new THREE.Sprite(material);
            textObj.scale.set(0.5 * 200, 0.25 * 200, 0.75 * 200);
            textObj.position.set(x,y,z);
            this.scene.add( textObj );
        },

        initText(){
            this.createSpriteText2(this.messageHouseNum+'',0,300,0);
        },


        //7.OBJ
        initObject() {
            // this.createFloor();
            // this.createLayout();
            this.initText();
        },

        onDocumentMouseMove(event) {
            event.preventDefault();
            this.mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
            this.mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;
        },

        render() {
            // requestAnimationFrame(this.render);
            this.renderer.render(this.scene, this.camera);
            this.renderer.autoClear = false;
            this.raycaster.setFromCamera(this.mouse, this.camera);
            let intersects = this.raycaster.intersectObjects(this.scene.children);
            if (intersects.length > 0) {
                if (this.INTERSECTED != intersects[0].object) {
                    if (this.INTERSECTED) this.INTERSECTED.material.color.setHex(this.INTERSECTED.currentHex);
                    this.INTERSECTED = intersects[0].object;
                    this.INTERSECTED.currentHex = this.INTERSECTED.material.color.getHex();
                    this.INTERSECTED.material.color.set( 0xBED264 );
                }
            } else {
                if (this.INTERSECTED) this.INTERSECTED.material.color.set(this.INTERSECTED.currentHex);
                this.INTERSECTED = null;
            }
        },
        

        animate(){
            requestAnimationFrame( this.animate );
            this.renderer.render( this.scene, this.camera );
            TWEEN.update();
            this.update();
            this.render();
        },

        update(){
            let delta = this.clock.getDelta();
            let moveDistance = 200 * delta;
            let rotateAngle = Math.PI / 2 * delta;
            this.controls.update();
        },
        

        //
        init(){
            this.initScene();
            this.initCamera();
            this.initRender();
            this.initEvent();
            this.initControls();
            this.initLight();
            this.initObject();
        },
        sendMsgToParent(){
            $('.opentableHouse').animate({right:'-750'},200);
            let expandedHouse = false;
            this.$emit('listenToChildEventMj',expandedHouse)
        }

    },

    watch: {
        messageHouseId: function(newVal,oldVal){
            this.drawBuilding();
            // this.drawCharts(newVal);
        }
    },

    components: {

    }

}

</script>

