<template>
    <div class="mjleftOpener">
        <div class="mjImageCont">
            <div class="ImageCont">
                <img src="../assets/images/menjin.png" v-if="deviceType ===''">
                <img src="../assets/images/renliankakou.png" v-else-if="deviceType ===''">
                <img src="../assets/images/renxingdaozha.png" v-else-if="deviceType ===''">
                <img src="../assets/images/rfid.png" v-else-if="deviceType ==='RFID'">
                <img src="../assets/images/cheliangdaozha.png" v-else-if="deviceType ===''">
                <img src="../assets/images/nengyuan.png" v-else-if="deviceType ===''">
				<img src="../assets/images/wifixt.png" v-else-if="deviceType ==='WIFI'">
                <img src="../assets/images/zhoujie.png" v-else>
            </div>
            <div class="textCont">
                <div class="mgbt">
                    <p class="rk">{{deviceType}}</p>
                    <p class="rk2">
                        <span class="num">{{deviceNum}}</span>
                        <span>/</span>
                    </p>
                </div>
                <div class="mgbt">
                    <p class="rk">
                        {{deviceBrand}}
                    </p>
                </div>
                <div class="mgbt">
                    <p class="rk">
                        {{deviceModel}}
                    </p>
                </div>
                <!-- <div class="mgbt">
                  <p class="rk">
                    <i class="el-icon-caret-right"></i>
                    <span class="cs">{{deviceVender}}</span>
                  </p>
                </div> -->
            </div>
        </div>
        <div class="separator"></div>
        <div class="mjDetail">
            <div class="mgbt2">
                <p class="rk">
                    <i class="el-icon-caret-right"></i>
                </p>
                <p>{{deviceParam}}</p>
                <p>{{deviceVersion}}</p>
                <p>{{produceTime |formatDate2}}</p>
                <p>{{producePlace}}</p>
                <p>{{venderPhone}}</p>
            </div>
            <div class="mgbt2">
                <p class="rk">
                    <i class="el-icon-caret-right"></i>
                </p>
                <p>027-13995654913</p>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    name: 'mjleftPane',
    props: ['community'],
    data() {
        return {
            deviceType: '',
            deviceParam: null,
            deviceVersion: null,
            produceTime: null,
            producePlace: null,
            venderPhone: null,
            deviceNum: null,
            deviceBrand: null,
            deviceModel: null,
            deviceVender: null,
            door: {
                deviceType: null
            }
        };
    },
    filters: {
        formatDate2(input) {
            var d = new Date(input);
            var year = d.getFullYear();
            var month = d.getMonth() + 1;
            var day = d.getDate() <10 ? '0' + d.getDate() : '' + d.getDate();
            return  year+ '-' + month + '-' + day;
        }
    },
    mounted() {
        // this.getInfo()
    },
    methods: {
        getInfo(data) {
            this.deviceType = data;

        }
    }
};
</script>

<style lang="scss" scoped>
//
.mjleftOpener {
    width: 443px;
    height: 800px;
    position: absolute;
    top: 100px;
    right: -450px;
    text-align: left;
    box-sizing: border-box;
    z-index: 1000;
    background: url('../assets/images/shebei_bg.png') no-repeat;
    background-size: 100% 100%;
    padding: 0;
}
.mjImageCont {
    padding: 30px 30px 25px 40px;
    overflow: hidden;
}
.separator{
    width: 100%;
    height: 2px;
    margin-bottom: 23px;
    background-image: linear-gradient(to right,rgba(0, 192, 255, 0) 0%,rgba(0, 191, 254, 0.8) 100%);
}
.mjDetail{
    padding: 0 40px;
}
.ImageCont {
    // position: relative;
    float: left;
    width: 174px;
    height: 197px;
    line-height: 197px;
    text-align: center;
    border: 1px rgba(0,192,255,0.47) solid;
    background-image: linear-gradient(-90deg, rgba(0,23,71,0.5) 0%, rgba(0,15,53,0.2) 100%);
    // background: url("../assets/images/deviceBackground.png") center no-repeat;
    // background-size: 100% 100%;
}
.textCont {
    float: right;
    width: 170px;
    padding-top: 15px;
}
.ImageCont img {
    vertical-align: middle;
    // width: 100%;
    // height: 100%;
}
.rk {
    color: #fff;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 10px;
}
.rk2 {
    color: #ffdb4f;
    font-size: 18px;
    line-height: 1.2;
}
.mgbt {
    margin-bottom: 10px;
}
.num{
    font-size: 40px;
}
.mgbt2 {
    margin-bottom: 30px;
}
.mgbt2 p {
    color: #fff;
    font-size: 14px;
    line-height: 24px;
}
.el-icon-caret-right{
    margin-right: 8px;
}
</style>



