<template>
    <div class="opentableMoke">
        <h3 class="tableTitle">
            <i class="el-icon-close" @click="sendMsgToParent"></i>  ({{messageSmokeNum}}) 
        </h3>
        <!-- ({{messageSmokeNum}}) -->
        <div class="tabOpener">
            <el-form :label-position="labelPosition" label-width="80px">
                <el-row>
                    <el-col :span="5">
                        <el-form-item label="">
                            <el-input v-model="listQuery.alarmCommand"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="16">
                        <el-form-item label="">
                            <el-col :span="11">
                                <el-date-picker type="datetime" placeholder="" v-model="listQuery.beginTime" style="width: 100%;" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss"></el-date-picker>
                            </el-col>
                            <el-col align="center" :span="2">-</el-col>
                            <el-col :span="11">
                                <el-date-picker type="datetime" placeholder="" v-model="listQuery.endTime" style="width: 100%;" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss"></el-date-picker>
                            </el-col>
                        </el-form-item>
                    </el-col>
                    <el-col :span="3">
                        <el-form-item label-width="10px">
                            <el-button class="pubBtn" type="primary" size="small" @click="handleFilter"></el-button>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>

            <div class="table-con">
                <el-table :data="list">
                    <el-table-column align="center" label="" width="60">
                        <template slot-scope="scope">
                            <span>{{scope.$index + (listQuery.pageNum-1)*listQuery.pageSize +1}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column align="center" label="" width="80">
                        <template slot-scope="scope">
                            <span>{{scope.row.alarmCommand}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column align="center" label="" width="120">
                        <template slot-scope="scope">
                            <span>{{scope.row.alarmContent}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column align="center" label="" width="160">
                        <template slot-scope="scope">
                            <span>{{scope.row.alarmTime | formatDate }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column align="center" label="" width="100">
                        <template slot-scope="scope">
                            <span>{{scope.row.energyConsumption}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column align="center" label="" width="100">
                        <template slot-scope="scope">
                            <span>{{scope.row.signalStrength}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column align="center" label="">
                        <template slot-scope="scope">
                            <span>{{scope.row.voltage}}</span>
                        </template>
                    </el-table-column>
                </el-table>
            </div>

            <el-row>
                <el-col :span="24" align="center">
                    <el-pagination @current-change="handleCurrentChange" :current-page="listQuery.pageNum" :page-size="listQuery.pageSize" layout="total, prev, pager, next, jumper" :total="total">
                    </el-pagination>
                </el-col>
                <!-- <el-col :span="6" class="f_right">
                    <el-button type="primary" size="small"></el-button>
                </el-col> -->
            </el-row>
        </div>
    </div>

</template>
<script>
import { formatDate } from '@/assets/js/utils'

const yesterday = formatDate(new Date().getTime() - 24 * 60 * 60 * 1000);
const today = formatDate(new Date().getTime());
export default {
    props:['community','messageSmokeNum'],
    data() {
        return {
            list: null,
            total: null,
            listQuery: {
                deviceNo: '',
                alarmCommand: '',
                beginTime: yesterday,
                endTime: today,
                pageNum: 1,
                pageSize: 12,
                communityId: this.community
            },
            labelPosition: 'right'
        };
    },
    mounted() {
        // this.getList()
    },
    methods: {
        getList(deviceNo) {
            if(deviceNo){
                this.listQuery.deviceNo = deviceNo;
            }

        },
        handleCurrentChange(val) {
            this.listQuery.pageNum = val;
            this.getList();
        },
        handleFilter() {
            this.listQuery.pageNum = 1;
            this.getList();
        },
        sendMsgToParent() {
            $('.opentableMoke').animate({ right: '-750' }, 100);
        }
    }
};
</script>

<style lang="scss" scoped>
.opentableMoke {
    box-sizing:border-box;
    -moz-box-sizing:border-box; /* Firefox */
    -webkit-box-sizing:border-box; /* Safari */
    width: 750px;
    height: 800px;
    position: fixed;
    right: -750px;
    z-index: 1000;
    top: 50%;
    transform: translateY(-45%);
    background-image: linear-gradient(-90deg,rgba(0, 59, 113, 0.99) 0%,rgba(0, 36, 102, 0.92) 100%);
    border: 1px solid #00c0ff;
    border-radius: 5px;
}
.tableTitle {
  margin: 0 0 10px 0;
  padding: 0 20px;
  text-align: left;
  line-height: 80px;
  font-size: 30px;
  height: 80px;
  color: #00f8ff;
  border-bottom: 1px solid #00c0ff;
  //   background: url("../assets/images/tableTitleBg.png") left no-repeat;
  i {
    float: right;
    cursor: pointer;
  }
}

.tabOpener {
  width: 750px;
  height: 645px;
  padding: 10px 10px;
  border-radius: 4px;
  color: #f8f8f8;
  text-align: left;
  box-sizing: border-box;
}
.table-con {
  box-sizing: border-box;
  -moz-box-sizing: border-box; /* Firefox */
  -webkit-box-sizing: border-box; /* Safari */
  height: 560px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 4px 4px 0 4px;
  margin: 0 0 20px 0;
  padding: 0 10px 10px 10px;
}
.imgCont {
  height: 100%;
}
.f_right {
  text-align: right;
}
.pubBtn {
  width: 80px;
  height: 36px;
  background-image: linear-gradient(-180deg, #216bff 0%, #21ccfe 100%);
  border-radius: 4px;
}
.el-icon-close {
  line-height: 80px;
}
</style>



