<template>
    <div class="rightOpener">
        <el-row>
            <el-col :span="24">
                <div class="mgbt">
                    <p class="title_m"></p>
                    <p class="rk2">
                        <!-- <a onclick="window.parent.location.href='http://58.49.35.14:9300/people/overview'" href="javascript:void(0)"> -->
                        <a onclick="window.parent.location.href='http://58.49.35.14:9100/people/overview'" href="javascript:void(0)">
                            <span class="numSize" id="num1"> {{totalPeople}} </span><span>/</span>
                        </a>
                    </p>
                </div>
                <div class="mgbt">
                    <p class="title_m"></p>
                    <p class="rk2">
                        <!-- <a onclick="window.parent.location.href='http://58.49.35.14:9300/house/manage'" href="javascript:void(0)"> -->
                        <a onclick="window.parent.location.href='http://58.49.35.14:9100/house/manage'" href="javascript:void(0)">
                            <span class="numSize" id="num2"> {{totalHouse}} </span><span>/</span>
                        </a>
                    </p>
                </div>
                <div class="mgbt">
                    <p class="title_m"></p>
                    <p class="rk2">
                        <!-- <a onclick="window.parent.location.href='http://58.49.35.14:9300/car/carOverview'" href="javascript:void(0)"> -->
                        <a onclick="window.parent.location.href='http://58.49.35.14:9100/car/carOverview'" href="javascript:void(0)">
                            <span class="numSize" id="num3"> {{totalCar}} </span><span>/</span>
                        </a>
                    </p>
                </div>
                <div class="mgbt">
                    <p class="title_m"></p>
                    <p class="rk3">
                        <!-- <a onclick="window.parent.location.href='http://58.49.35.14:9300/people/important'" href="javascript:void(0)"> -->
                        <a onclick="window.parent.location.href='http://58.49.35.14:9100/people/important'" href="javascript:void(0)">
                            <span class="numSize" id="num4"> {{keyPeople}} </span><span>/</span>
                        </a>
                    </p>
                </div>
                <div class="mgbt">
                    <p class="title_m"></p>
                    <p class="rk3">
                        <!-- <a onclick="window.parent.location.href='http://58.49.35.14:9300/people/focus'" href="javascript:void(0)"> -->
                        <a onclick="window.parent.location.href='http://58.49.35.14:9100/people/focus'" href="javascript:void(0)">
                            <span class="numSize" id="num5"> {{keyConcern}} </span><span>/</span>
                        </a>
                    </p>
                </div>
                <div class="mgbt">
                    <p class="title_m"></p>
                    <p class="rk3">
                        <!-- <a onclick="window.parent.location.href='http://58.49.35.14:9300/car/carOverview'" href="javascript:void(0)"> -->
                        <a onclick="window.parent.location.href='http://58.49.35.14:9100/car/carOverview'" href="javascript:void(0)">
                            <span class="numSize" id="num6"> {{keyCar}} </span><span>/</span>
                        </a>
                    </p>
                </div>
            </el-col>
        </el-row>

        <div class="device_chart">
            <p class="title_s"></p>
            <div id="c-device" style="width:100%; height:130px;"></div>
        </div>
    </div>
</template>
<script>
import echarts from 'echarts'
// import { getTotalPeopleHouse, getKeyPeople, getTotalCar, getDeviceType } from '@/api/homePage';
export default {
    name: 'rightPane',
    props: ['community'],
    data() {
        let commId = 10000 
        return {
            totalPeople: 3000,
            totalHouse: 1000,
            totalCar: 1200,
            keyPeople: 20,
            keyConcern: 12,
            keyCar: 20
        }
    },
    mounted() {
        this.getInitNum();
    },
    methods: {
        getInitNum() {
            //
            let arr1 = ['','','','RFID','','',''];
            let arr2 = [10,12,15,8,4,4,2];
            let arr3 = [5,6,5,5,3,3,2];
            this.r_drawPieChart(arr1, arr2, arr3);
        },

        r_drawPieChart(data1, data2, data3) {
            let r_pieChart = echarts.init(document.getElementById('c-device'));
            r_pieChart.setOption({
                tooltip: {
                    trigger: 'item'
                },
                legend: {
                    x: 'right',
                    y: 'top',
                    itemWidth: 22,
                    itemHeight: 12,
                    textStyle: {
                        color: '#fff',
                        fontSize: 14
                    },
                    data:['','']
                },
                grid: {
                    x: 10,
                    y: 20,
                    x2: 0,
                    y2: 25
                },
                xAxis: [
                    {
                        type: 'category',
                        axisLine: {
                            show: false
                        },
                        axisTick: {
                            show: false
                        },
                        axisLabel: {
                            show: true,
                            textStyle: {
                                color: '#fff',
                                fontSize: 14
                            }
                        },
                        data: data1
                    }
                ],
                yAxis: [
                    {
                        type: 'value',
                        show: false
                    }
                ],
                series: [
                    {
                        name: '',
                        type: 'bar',
                        barWidth: 18,
                        barMinHeight: 1,
                        itemStyle: {
                            normal: {
                                color: '#1FF6B3',
                                barBorderRadius: 10,
                                label: {
                                    show: true,
                                    position: 'top',
                                    formatter: '{c}',
                                    textStyle: {
                                        color: '#FBDB3E',
                                        fontSize: 14
                                    }
                                }
                            }
                        },
                        data: data2
                    },
                    {
                        name: '',
                        type: 'bar',
                        barWidth: 18,
                        barMinHeight: 1,
                        itemStyle: {
                            normal: {
                                color: '#FFDB4F',
                                barBorderRadius: 10,
                                label: {
                                    show: true,
                                    position: 'top',
                                    formatter: '{c}',
                                    textStyle: {
                                        color: '#FBDB3E',
                                        fontSize: 14
                                    }
                                }
                            }
                        },
                        data: data3
                    }
                ]
            });

        }

    }
}

</script>

<style lang="scss" scoped>

// 
.rightOpener{
    width: 350px;
    height: 100%;
    position:absolute;
    left: 60px;
    padding-top: 50px;
    color: #fff;
    text-align: left;
    box-sizing: border-box;
    -moz-box-sizing:border-box; /* Firefox */
    -webkit-box-sizing:border-box; /* Safari */
}
.device_chart{
    width: 520px;
    margin-top: 20px;
}
.rk2 {
    color: #ffdb4f;
    font-size: 18px;
    padding-left: 20px;
}
.rk2 a {
    color: #ffdb4f;
    font-size: 18px;
}
.rk3 {
    color: #1FF6B3;
    font-size: 18px;
    padding-left: 20px;
}
.rk3 a {
    color: #1FF6B3;
    font-size: 18px;
}
.mgbt{
    margin-bottom: 30px;
}
.numSize{
    font-size: 40px;
    margin-right: 5px;
}
</style>



