<template>
    <div class="opentableMj">
        <h3 class="tableTitle">
            <i class="el-icon-close" @click="sendMsgToParent"></i> {{messageGuardNum}}
        </h3>
        <!-- {{messageGuardNum}} -->
        <div class="tabOpener">
            <el-form :label-position="labelPosition" label-width="80px">
                <el-row>
                    <el-col :span="18" :gutter="10">
                        <el-form-item label="">
                            <el-col :span="11">
                                <el-date-picker type="datetime" placeholder="" v-model="listQuery.beginTime" style="width: 100%;" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss"></el-date-picker>
                            </el-col>
                            <el-col align="center" :span="2">-</el-col>
                            <el-col :span="11">
                                <el-date-picker type="datetime" placeholder="" v-model="listQuery.endTime" style="width: 100%;" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss"></el-date-picker>
                            </el-col>
                        </el-form-item>
                    </el-col>
                    <!-- <el-col :span="6">
                        <el-form-item label="" label-width="80px">
                            <el-select v-model="listQuery.isKey" placeholder="">
                                <el-option label="" value=""></el-option>
                                <el-option label="" value="true"></el-option>
                                <el-option label="" value="false"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col> -->
                    <el-col :span="3">
                        <el-form-item label-width="10px">
                            <el-button class="pubBtn" type="primary" size="small" @click="handleFilter"></el-button>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>

            <div class="table-con">
                <el-table :data="list" @row-click="rowClick">
                    <el-table-column align="center" label="" width="60">
                        <template slot-scope="scope">
                            <span>{{scope.$index + (listQuery.pageNum-1)*listQuery.pageSize +1}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column align="center" label="" width="80">
                        <template slot-scope="scope">
                            <span>{{scope.row.name}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column align="center" label="" width="120">
                        <template slot-scope="scope">
                            <span>{{scope.row.openType}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column align="center" label="" width="180">
                        <template slot-scope="scope">
                            <span>{{scope.row.accessTime | formatDate }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column align="center" label="" width="180">
                        <template slot-scope="scope">
                            <span>{{scope.row.devicePlace}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column align="center" label="">
                        <template slot-scope="scope">
                            <span>{{scope.row.isKey}}</span>
                        </template>
                    </el-table-column>
                    <!-- <el-table-column align="center" label="">
                        <template slot-scope="scope">
                            <span>{{scope.row.communityName+scope.row.address}}</span>
                        </template>
                    </el-table-column> -->
                </el-table>
            </div>

            <el-row>
                <el-col :span="24" align="center">
                    <el-pagination @current-change="handleCurrentChange" :current-page="listQuery.pageNum" :page-size="listQuery.pageSize" layout="total, prev, pager, next, jumper" :total="total">
                    </el-pagination>
                </el-col>
                <!-- <el-col :span="6" class="f_right">
                    <el-button type="primary" size="small" @click="exportTable"></el-button>
                </el-col> -->
            </el-row>
        </div>
        <div class="mj-photo">
            <i class="el-icon-close" @click="closePhoto"></i>
            <img :src="photoUrl">
        </div>
    </div>
</template>
<script>
import { formatDate } from '@/assets/js/utils'

const yesterday = formatDate(new Date().getTime() - 24 * 60 * 60 * 1000);
const today = formatDate(new Date().getTime());
export default {
    name: 'mjTabPane',
    props: ['community', 'messageGuardNum'],
    data() {
        return {
            list: [{
                accessTime: '2018-05-02 15:30',
                carNo:'A56565',
                telphone:13985258798,
                openType:'',
                name: '',
                devicePlace: '',
                isKey: ''
            }, {
                accessTime: '2018-05-02 15:30',
                carNo:'A56565',
                telphone:13985258798,
                openType:'',
                name: '',
                devicePlace: '',
                isKey: ''
            }, {
                accessTime: '2018-05-02 15:30',
                carNo:'A56565',
                telphone:13985258798,
                openType:'',
                name: '',
                devicePlace: '',
                isKey: ''
            },{
                accessTime: '2018-05-02 15:30',
                carNo:'A56565',
                telphone:13985258798,
                openType:'',
                name: '',
                devicePlace: '',
                isKey: ''
            },{
                accessTime: '2018-05-02 15:30',
                carNo:'A56565',
                telphone:13985258798,
                openType:'',
                name: '',
                devicePlace: '',
                isKey: ''
            },{
                accessTime: '2018-05-02 15:30',
                carNo:'A56565',
                telphone:13985258798,
                openType:'',
                name: '',
                devicePlace: '',
                isKey: ''
            }, {
                accessTime: '2018-05-02 15:30',
                carNo:'A56565',
                telphone:13985258798,
                openType:'',
                name: '',
                devicePlace: '',
                isKey: ''
            },{
                accessTime: '2018-05-02 15:30',
                carNo:'A56565',
                telphone:13985258798,
                openType:'',
                name: '',
                devicePlace: '',
                isKey: ''
            },{
                accessTime: '2018-05-02 15:30',
                carNo:'A56565',
                telphone:13985258798,
                openType:'',
                name: '',
                devicePlace: '',
                isKey: ''
            },{
                accessTime: '2018-05-02 15:30',
                carNo:'A56565',
                telphone:13985258798,
                openType:'',
                name: '',
                devicePlace: '',
                isKey: ''
            }],
            total: null,
            photoUrl: 'https://sc-wc-hjhy.oss-cn-hangzhou.aliyuncs.com/10032/open_door_face/20181031/1540975605_zhlps4gt.jpeg',
            listQuery: {
                deviceNo: '',
                beginTime: yesterday,
                endTime: today,
                isKey: '',
                pageNum: 1,
                pageSize: 12,
                communityId: this.community
            },
            labelPosition: 'right'
        };
    },
    mounted() {
        // this.getList();
    },
    methods: {
        getList(deviceNo) {
            if(deviceNo){
                this.listQuery.deviceNo = deviceNo;
            }
        },
        handleCurrentChange(val) {
            this.listQuery.pageNum = val;
            this.getList();
        },
        handleFilter() {
            this.listQuery.pageNum = 1;
            this.getList();
        },
        sendMsgToParent() {
            $('.opentableMj').animate({ right: '-750' }, 500);
            $('.mj-photo').hide();
        },
        rowClick(row){
            $('.mj-photo').show();
            if(row.photoNo){
                this.photoUrl = 'https://sc-res-images.oss-cn-hangzhou.aliyuncs.com/' + row.photoNo;
                // this.photoUrl = 'http://59.43.6.127:9100/' + row.photoNo;
            }
        },
        closePhoto(){
            $('.mj-photo').hide();
        }
    }
};
</script>

<style lang="scss" scoped>
.opentableMj {
    box-sizing:border-box;
    -moz-box-sizing:border-box; /* Firefox */
    -webkit-box-sizing:border-box; /* Safari */
    width: 750px;
    height: 800px;
    position: fixed;
    right: -750px;
    z-index: 1000;
    top: 50%;
    transform: translateY(-45%);
    border: 1px solid #00c0ff;
    border-radius: 5px;
    background-image: -webkit-linear-gradient(-90deg,rgba(0, 59, 113, 0.99) 0%,rgba(0, 36, 102, 0.92) 100%);
    background-image: -o-linear-gradient(-90deg,rgba(0, 59, 113, 0.99) 0%,rgba(0, 36, 102, 0.92) 100%);
    background-image: -moz-linear-gradient(-90deg,rgba(0, 59, 113, 0.99) 0%,rgba(0, 36, 102, 0.92) 100%);
    background-image: linear-gradient(-90deg,rgba(0, 59, 113, 0.99) 0%,rgba(0, 36, 102, 0.92) 100%);

}
.tableTitle {
    margin: 0 0 10px 0;
    padding: 0 20px;
    text-align: left;
    color: #00f8ff;
    line-height: 80px;
    font-size: 30px;
    height: 80px;
    border-bottom: 1px solid #00c0ff;
    //   background: url("../assets/images/tableTitleBg.png") left no-repeat;
    i {
        float: right;
        cursor: pointer;
    }
}
.tabOpener {
    width: 750px;
    height: 645px;
    //   background: rgba(8, 26, 59, 0.9);
    padding: 10px 10px;
    color: #f8f8f8;
    text-align: left;
    box-sizing: border-box;
}
.table-con {
    box-sizing: border-box;
    -moz-box-sizing: border-box; /* Firefox */
    -webkit-box-sizing: border-box; /* Safari */
    height: 560px;
    background: rgba(0, 0, 0, 0.1);
    border: 1px solid #00c0ff;
    border-radius: 4px;
    margin: 0 0 20px 0;
    padding: 0 10px 10px 10px;
}
.imgCont {
    height: 100%;
}
.f_right {
    text-align: right;
}
.pubBtn {
    width: 80px;
    height: 36px;
    background-image: linear-gradient(-180deg, #216bff 0%, #21ccfe 100%);
    border-radius: 4px;
}
.el-icon-close {
    line-height: 80px;
}
.mj-photo{
    position:absolute;
    top:50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    width: 400px;
    background: rgba(8,26,59,0.90);
    padding: 20px;
    border: 1px solid #2463d5;
    border-radius: 4px;
    box-sizing:border-box;
    -moz-box-sizing:border-box; /* Firefox */
    -webkit-box-sizing:border-box; /* Safari */
    display: none;
    i{
        position: absolute;
        right: 5px;
        top: 5px;
        color: #fff;
        cursor: pointer;
    }
    img{
        width: 100%;
    }
}
.mj-photo .el-icon-close{
    line-height: 24px;
}
</style>



