<template>
    <div class="leftOpener">
        <div class="bt">
            <p class="title_m"></p>
            <div id="l_pieChart1" style="width:100%; height:150px;"></div>
        </div>
        <div class="mgbt">
            <p class="title_m"></p>
            <div id="l_pieChart2" style="width:100%; height:140px;"></div>
        </div>
        <div class="bt">
            <p class="title_m"></p>
            <div id="l_pieChart3" style="width:100%; height:150px;"></div>
        </div>
        <div>
            <p class="title_m"></p>
            <div id="l_pieChart4" style="width:100%; height:260px;"></div>
        </div>
    </div>
</template>

<script>
import echarts from 'echarts'
export default {
    name: 'leftPane',
    props:['community'],
    data() {
        return {

        }
    },
    mounted() {
        this.getInitNum()
    },
    methods: {
        getInitNum() {
            //
            let arr1 = [];
            arr1[''] = 500;
            arr1[''] = 200;
            // arr[''] = 100;
            this.l_drawPieChart1(arr1);

            //
            let arr = [];
            // arr = [500, 300, 200];
            arr[''] = 450;
            arr[''] = 300;
            arr[''] = 350;
            let arr2 = [arr[''],arr[''],arr['']];
            this.l_drawPieChart2(arr2);
            
            //
            let arr3 = [352,150];
            this.l_drawPieChart3(arr3);

            //
            let arr4 = [10,5,6,2,3,5,8];
            let arr5 = ['','','','','','','',''];
            this.l_drawPieChart4(arr4, arr5);
        },

        l_drawPieChart1(data) {
            let l_pieChart1 = echarts.init(document.getElementById('l_pieChart1'));
            let dataStyle = {
                normal: {
                    label: {show:false},
                    labelLine: {show:false}
                }
            };
            let placeHolderStyle = {
                normal : {
                    color: 'rgba(0,0,0,0)',
                    label: {show:false},
                    labelLine: {show:false}
                },
                emphasis : {
                    color: 'rgba(0,0,0,0)'
                }
            };

            l_pieChart1.setOption({
                tooltip : {
                    show: true,
                    formatter: '{b}: {c} ({d}%)'
                },
                legend: {
                    orient : 'vertical',
                    x : '56%',
                    y : 'center',
                    icon: 'circle',
                    itemGap: 24,
                    data: ['',''],
                    formatter: function(name){
                        return '{a|'+ name +'}  {b|'+ data[name]+'}';
                    },
                    textStyle: {
                        rich: {
                            a: {
                                color: '#fff',
                                fontSize: 14
                            },
                            b: {
                                color: '#EDD563',
                                fontSize: 14
                            }
                        }
                    }
                },
                color: ['#00D8FF','#1FFF8E','#DFFB7A'],
                series : [
                    {
                        type: 'pie',
                        clockWise: true,   //
                        center: ['30%','48%'],
                        radius: [50, 60],
                        itemStyle : dataStyle,
                        data:[
                            {
                                value:data[''],
                                name:''
                            },
                            {
                                value: parseInt(data['']),
                                name:'',
                                itemStyle : {
                                    normal: {
                                        color: 'rgba(0,216,255,0.1)'
                                    }
                                }
                            }
                        ]
                    },
                    {
                        type:'pie',
                        clockWise: true,
                        center: ['30%','48%'],
                        radius : [36, 46],
                        itemStyle : dataStyle,
                        data:[
                            {
                                value:data[''], 
                                name:''
                            },
                            {
                                value: parseInt(data['']),
                                name:'',
                                itemStyle : {
                                    normal: {
                                        color: 'rgba(31,255,142,0.1)'
                                    }
                                }
                            }
                        ]
                    }
                    // {
                    //     type:'pie',
                    //     clockWise: true,
                    //     center: ['30%','48%'],
                    //     radius : [22, 32],
                    //     itemStyle : dataStyle,
                    //     data:[
                    //         {
                    //             value:data[''], 
                    //             name:''
                    //         },
                    //         {
                    //             value: parseInt(data['']) + parseInt(data['']),
                    //             name:'',
                    //             itemStyle : {
                    //                 normal: {
                    //                     color: 'rgba(223,251,122,0.1)'
                    //                 }
                    //             }
                    //         }
                    //     ]
                    // }
                ]
            });
        },

        l_drawPieChart2(data) {
            let l_pieChart2 = echarts.init(document.getElementById('l_pieChart2'));
            l_pieChart2.setOption({
                tooltip: {
                    trigger: 'item'
                },
                grid: {
                    x: 10,
                    y: 15,
                    x2: 10,
                    y2: 0,
                    containLabel: true
                },
                xAxis: [
                    {
                        type: 'category',
                        axisLine: {
                            show: false
                        },
                        axisTick: {
                            show: false
                        },
                        axisLabel: {
                            show: true,
                            textStyle: {
                                color: '#fff',
                                fontSize: 14
                            }
                        },
                        // show: false,
                        data: ['', '','']
                    }
                ],
                yAxis: [
                    {
                        type: 'value',
                        show: false
                    }
                ],
                series: [
                    {
                        name: '',
                        type: 'bar',
                        barWidth: 50,
                        itemStyle: {
                            normal: {
                                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                    offset: 0,
                                    color: '#1EEDFF'
                                }, {
                                    offset: 1,
                                    color: '#FFF35B'
                                }]),
                                barBorderRadius: 4,
                                label: {
                                    show: true,
                                    position: 'top',
                                    formatter: '{c}',
                                    textStyle: {
                                        color: '#EDD563',
                                        fontSize: 14
                                    }
                                }
                            }
                        },
                        data: data
                    }
                ]
            });
        },

        // l_drawPieChart3(data) {
        //     let l_pieChart3 = echarts.init(document.getElementById('l_pieChart3'));
        //     l_pieChart3.setOption({
        //         tooltip : {
        //             trigger: 'item',
        //             formatter: '{a} <br/>{b} : {d}%'
        //         },
        //         grid: {

        //         },
        //         series : [
        //             {
        //                 name:'',
        //                 type:'pie',
        //                 radius : [40, 60],
        //                 color: ['#64EFA9','#FF885D','#34C6D3','#DEFF4A','#B6A2DE','#F7B4A9','#5AB1EF'],
        //                 itemStyle: {
        //                     normal: {
        //                         label: {
        //                             formatter: '{class1|{b}}\n{class2|{c}}',
        //                             rich:{
        //                                 class1: {
        //                                     fontSize: 12,
        //                                     lineHeight: 16
        //                                 },
        //                                 class2: {
        //                                     color: '#FFDE3D',
        //                                     fontSize: 18,
        //                                     lineHeight: 22
        //                                 }
        //                             }
        //                         }
        //                     }
        //                 },
        //                 data: data
        //             }
        //         ]
        //     });
        // },

        l_drawPieChart3(data) {
            let l_pieChart3 = echarts.init(document.getElementById('l_pieChart3'));
            l_pieChart3.setOption({
                tooltip : {
                    trigger: 'axis',
                    axisPointer: {
                        type: 'none'
                    },
                    formatter: '{b} : {c}'
                },
                xAxis: {
                    data: ['',''],
                    axisTick: {show: false},
                    axisLine: {show: false},
                    axisLabel: {
                        textStyle: {
                            color: '#fff',
                            fontSize: 14
                        }
                    }
                },
                yAxis: {
                    splitLine: {show: false},
                    axisTick: {show: false},
                    axisLine: {show: false},
                    axisLabel: {show: false}
                },
                grid: {
                    x: 50,
                    y: 20,
                    x2: 50,
                    y2: 35
                },
                series : [
                    {
                        type:'pictorialBar',
                        barCategoryGap: '-50%',
                        barMinHeight: 10,
                        symbol: 'path://M0,10 L10,10 C5.5,10 5.5,5 5,0 C4.5,5 4.5,10 0,10 z',
                        // color: ['#64EFA9','#FF885D','#34C6D3','#DEFF4A','#B6A2DE','#F7B4A9','#5AB1EF'],
                        itemStyle: {
                            normal: {
                                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                    offset: 0,
                                    color: '#A4FFE8'
                                }, {
                                    offset: 1,
                                    color: '#FFF35B'
                                }]),
                                label: {
                                    show: true,
                                    position: 'top',
                                    textStyle: {
                                        color: '#EDD563',
                                        fontSize: 14
                                    }
                                }
                            }
                        },
                        data: data
                    }
                ]
            });
        },
        
        l_drawPieChart4(data1, data2) {
            let l_pieChart4 = echarts.init(document.getElementById('l_pieChart4'));
            var data = data1;
            var yMax = Math.max(...data);
            var dataShadow = [];
            var i = 0;
            if(yMax == 0){
                yMax = 1;
            }
            for(; i < data.length; i++) {
                dataShadow.push(yMax);
            };
            l_pieChart4.setOption({
                grid: {
                    x: 19,
                    y: 28,
                    x2: 10,
                    y2: 20
                },
                xAxis : [
                    {
                        show: false,
                        type : 'value'
                        // boundaryGap : [0, 0.01]
                    }
                ],
                yAxis : [
                    {
                        show: false,
                        type: 'category',
                        boundaryGap: false,
                        data: data2
                    }
                ],
                series : [
                    { // For shadow
                        type: 'bar',
                        barWidth: 14,
                        itemStyle: {
                            normal: {
                                color: 'rgba(45,192,213,0.24)',
                                barBorderRadius: 100
                            }
                        },
                        barGap:'-100%',
                        data: dataShadow,
                        animation: false
                    },
                    {
                        name:'',
                        type:'bar',
                        barWidth: 14,
                        itemStyle: {
                            normal: {
                                color: new echarts.graphic.LinearGradient(0, 0, 1, 0, [{
                                    offset: 0,
                                    color: '#3CDEFC'
                                }, {
                                    offset: 1,
                                    color: '#FFF35B'
                                }]),
                                barBorderRadius: 100,
                                label: {
                                    show: true,
                                    position: [2,-20],
                                    // distance: 10,
                                    formatter: '{class1|{b}}{class2|{c}}',
                                    rich:{
                                        class1: {
                                            width: 180,
                                            color: '#fff',
                                            fontSize: 14,
                                            lineHeight: 20
                                        },
                                        class2: {
                                            width: 107,
                                            align: 'right',
                                            color: '#EDD563',
                                            fontSize: 14,
                                            lineHeight: 20
                                        }
                                    }
                                }
                            }
                        },
                        data:data
                    }
                ]
            });
        }
    }
}

</script>

<style lang="scss" scoped>

// 
.leftOpener{
    width: 320px;
    height: 100%;
    position: absolute;
    right: 60px;
    padding-top: 50px;
    color: #fff;
    text-align: left;
    box-sizing: border-box;
    -moz-box-sizing:border-box; /* Firefox */
    -webkit-box-sizing:border-box; /* Safari */
}
.bt {
    margin-bottom: 10px;
}
.mgbt{
    margin-bottom: 30px;
}
.rk {
    color: #78aef2;
    margin-bottom: 10px;
}
.rk2 {
    color: #ffdb4f;
}
.rk3 {
    color: #7aff00;
}
.zd{
    font-size: 12px;
}
.zd span{
    float: right;
    margin-right: 70px;
}
</style>



