import axios from 'axios';
import qs from 'qs'

// let base = 'http://47.96.95.76:9300/scpm';
let base = 'http://localhost:9301/scpm';
// let base = '/scpm';
var instance = axios.create({
    headers: {'content-type': 'application/x-www-form-urlencoded'}
});
var instance2 = axios.create({
    headers: {'content-type': 'application/json;charset=UTF-8'}
});
export const getTotalPeopleHouse = params => {
    return instance.get(`${base}/community/community-people-house/${params}`);
}

export const getKeyPeople = params => {
    return instance.get(`${base}/personInfo/people-type/${params}`);
}

export const getTotalCar = params => {
    return instance.get(`${base}/community/statistics-community-car/${params}`);
}

export const getDeviceType = params => {
    return instance.get(`${base}/community/statistics-device-status/${params}`);
}

export const getPeopleByType = params => {
    return instance.get(`${base}/personInfo/statistics-live-type/${params}`);
}

export const getHouseByType = params => {
    return instance.get(`${base}/house/statistics-community-house/${params}`);
}

export const getCarByType = params => {
    return instance.get(`${base}/carDetail/statistics-car-type/${params}`);
}

export const getKeyPeopleByType = params => {
    return instance.get(`${base}/personInfo/important-people-type/${params}`);
}

export const getBuildingUnits = (data1,data2) => {
    return instance.get(`${base}/building/get-building-units/${data1}/${data2}`);
}

export const getBuildingUnitsNum = (data1,data2) => {
    return instance.get(`${base}/building/getBuildingStatics/${data1}/${data2}`);
}
export const getBuildingTotalNum = (data1,data2) => {
    return instance.get(`${base}/building/statistics-building-situation/${data1}/${data2}`);
}

export const getBuildingPeopleByType = (data1,data2) => {
    return instance.get(`${base}/building/statistic-live-type/${data1}/${data2}`);
}

export const getBuildingHouseByType = (data1,data2) => {
    return instance.get(`${base}/building/statistics-house-type/${data1}/${data2}`);
}

export const getBuildingCarByType = (data1,data2) => {
    return instance.get(`${base}/building/statistics-car-type/${data1}/${data2}`);
}

export const getBuildingKeyPeopleByType = (data1, data2) => {
    return instance.get(`${base}/building/statistics-key-people/${data1}/${data2}`);
}

export const getBuildingStruct = params => {
    var p = qs.stringify(params);
    return instance.post(`${base}/building/getBuildingStruct`, p);
}

export const getDeviceInfo = (data1) => {
    return instance.get(`${base}/community/community-device-detail/${data1}`);
}

export const getTotalHouse = (data1) => {
    return instance.post(`${base}/building/get-building-exclude-basic/${data1}`);
}

export const getDoorRecord = params => {
    return instance2.post(`${base}/device/pagelist-door-record`, params); 
}

export const getFaceRecord = params => {
    return instance2.post(`${base}/device/pagelist-face-record`, params);
}

export const getGatewayRecord = params => {
    return instance2.post(`${base}/device/pagelist-car-record`, params);
}

export const getRfidRecord = params => {
    return instance2.post(`${base}/device/pagelist-rfid-record`, params);
}

export const getEnergyRecord = params => {
    return instance2.post(`${base}/device/pagelist-energy-record`, params);
}

export const getAlarmRecord = params => {
    return instance2.post(`${base}/device/pagelist-alarm-record`, params);
}





