        ray(event){
            let Sx = event.clientX;
            let Sy = event.clientY;
            let x = ( Sx / window.innerWidth ) * 2 - 1;
            let y = -( Sy / window.innerHeight ) * 2 + 1;
            let standardVector  = new THREE.Vector3(x, y, 0.5);
            let worldVector = standardVector.unproject(this.camera);
            let ray = worldVector.sub(this.camera.position).normalize();
            let raycaster = new THREE.Raycaster(this.camera.position, ray);
            let objChildren = this.scene.children;
            let intersects = raycaster.intersectObjects(objChildren);
            if (intersects.length > 0) {
                let meshNumber = intersects[0].object.ID;
                let meshName = intersects[0].object.name;
                if(meshName.indexOf('house')>=0){
                    this.messageHouseNum = meshName.slice(5);
                    getTotalHouse(10029).then(response => {
                        let res = response.data.data;
                        for(let item of res){
                            if(item.buildingName == this.messageHouseNum+''){
                                if(item.id == '' || item.id == null){
                                    this.messageHouseId = 0;
                                }else{
                                    this.messageHouseId = item.id;
                                }
                            }
                        }
                    });
                    this.openDialogHouseDetailThree()
                }
            }

        },

        ray2(event){
            let Sx = event.clientX;
            let Sy = event.clientY;
            let x = ( Sx / window.innerWidth ) * 2 - 1;
            let y = -( Sy / window.innerHeight ) * 2 + 1;
            let standardVector  = new THREE.Vector3(x, y, 0.5);
            let worldVector = standardVector.unproject(this.camera);
            let ray = worldVector.sub(this.camera.position).normalize();
            let raycaster = new THREE.Raycaster(this.camera.position, ray);
            let objChildren = this.scene.children;
            let intersects = raycaster.intersectObjects(objChildren);
            if (intersects.length > 0) {
                let meshName = intersects[0].object.name;
                let meshNo = intersects[0].object.ID;
                if(meshName.indexOf('smoke')>=0){
                    this.messageSmokeNum = meshName.slice(5);
                    this.messageDeviceNo = meshNo;
                    this.$refs.childsmokeTable.getList(this.messageDeviceNo);
                    this.opener_tableMoke();
                }
                if(meshName.indexOf('guard')>=0){
                    this.messageGuardNum = meshName.slice(5);
                    this.messageDeviceNo = meshNo;
                    this.$refs.childmjTable.getList(this.messageDeviceNo);
                    this.opener_tableMj();
                }
                if(meshName.indexOf('peopl')>=0){
                    this.messagePeopleNum = meshName.slice(5);
                    this.messageDeviceNo = meshNo;
                    this.$refs.childpeopleTable.getList(this.messageDeviceNo);
                    this.opener_tablePeople();
                }
                if(meshName.indexOf('pgate')>=0){
                    this.messagePeoplegateNum = meshName.slice(5);
                    this.messageDeviceNo = meshNo;
                    this.$refs.childpeoplegateTable.getList(this.messageDeviceNo);
                    this.opener_tablePeoplegate();
                }
                if(meshName.indexOf('dgate')>=0){
                    this.messageDdcgateNum = meshName.slice(5);
                    this.messageDeviceNo = meshNo;
                    this.$refs.childddcgateTable.getList(this.messageDeviceNo);
                    this.opener_tableDdcgate();
                }
                if(meshName.indexOf('cgate')>=0){
                    this.messageClgateNum = meshName.slice(5);
                    this.messageDeviceNo = meshNo;
                    this.$refs.childclgateTable.getList(this.messageDeviceNo);
                    this.opener_tableClgate();
                }
                if(meshName.indexOf('cdzwl')>=0){
                    this.messageDzwlgateNum = meshName.slice(5);
                    this.messageDeviceNo = meshNo;
                    this.$refs.childdzwlgateTable.getList(this.messageDeviceNo);
                    this.opener_tableDzwlgate();
                }
                if(meshName.indexOf('cwifi')>=0){
                    this.$message({
                        message: 'WIFI',
                        type: 'warning'
                    });
                }
            }
        },

        showInformation(object,event){
            console.dir(object);
            $('#close').unbind().bind('click',function(){
                $('#message').css({'display':'none'});
            })
            $('#message').css({'display':'block','position':'fixed','left':event.clientX,'top':event.clientY})
        },

        initScene() {
            this.scene = new THREE.Scene();
        },

        initCamera() {
            this.camera = new THREE.PerspectiveCamera( this.VIEW_ANGLE, this.ASPECT, this.NEAR, this.FAR);
            this.camera.position.set(0,600,1900);
            this.camera.lookAt(this.scene.position);
            this.camera.lookAt(0,0,0);
            this.scene.add(this.camera);
        },

        initRender(){
            if ( Detector.webgl )
                this.renderer = new THREE.WebGLRenderer( {antialias:true,alpha:true} );
            else
                this.renderer = new THREE.CanvasRenderer();
            this.renderer.setPixelRatio( window.devicePixelRatio );
            this.renderer.setSize(this.SCREEN_WIDTH, this.SCREEN_HEIGHT);
            this.renderer.shadowMap.enabled = true;
            // this.renderer.shadowMapEnabled = true;
            this.renderer.shadowMapSoft = true;
            this.renderer.shadowMap.type = THREE.PCFSoftShadowMap; 
            this.container = document.getElementById( 'container' );
            this.container.appendChild( this.renderer.domElement );
            this.renderer.setClearColor(0x001528,0);
        },

        initEvent(){
            THREEx.WindowResize(this.renderer, this.camera);
            THREEx.FullScreen.bindKey({ charCode : 'm'.charCodeAt(0) });
        },

        initControls(){
            this.controls = new THREE.OrbitControls( this.camera, this.renderer.domElement );
            this.controls.enableKeys = false;
            this.controls.minPolarAngle = 0; // radians
            this.controls.maxPolarAngle = Math.PI*0.45; // radians
            this.controls.enablePan = true;
            this.controls.keyPanSpeed = 7.0;	
            this.controls.minDistance = 200; 
            this.controls.maxDistance = 3000; 
        },

        initLight(){

            let directionalLight = new THREE.DirectionalLight( 0xabfdff,1 );
            directionalLight.position.set( 0, 100, 0).normalize();
            this.scene.add( directionalLight );
      
            let ambient = new THREE.AmbientLight( 0xffffff,1 ); 
            ambient.position.set(0,0,0);
            this.scene.add( ambient );

            let light = new THREE.SpotLight(0x004C64,1);
            light.position.set(600,400,500);
            light.angle = Math.PI;
            this.scene.add(light);

        },


        //
        createFloor(){
            let loader=new THREE.TextureLoader();
            let _this = this;
            loader.load('static/images/ljyyMap.jpg',function(texture){
                texture.wrapS = texture.wrapT = THREE.RepeatWrapping;
                texture.repeat.set( 1, 1 );
                let floorGeometry = new THREE.BoxGeometry(2000, 1650, 10);
                let floorMaterial = new THREE.MeshBasicMaterial( { map: texture, side: THREE.DoubleSide} );
                let floor = new THREE.Mesh(floorGeometry, floorMaterial);
                floor.material.color.set( 0x398ad6 );
                floor.position.y = -0.5;
                floor.rotation.x = Math.PI / 2;
                floor.position.y = -200;
                floor.name = 'floor';
                _this.dummy.add(floor);
                _this.scene.add(_this.dummy);
            });
        },

