        createSpriteText2(text,x,y,z){
            let canvas = document.createElement('canvas');
            canvas.width=256;
            canvas.height=128;
            let ctx = canvas.getContext('2d');
            ctx.fillStyle = '#bbf42f';
            ctx.font = 'Bold 70px Arial';
            ctx.lineWidth = 4;
            ctx.fillText(text,4,104);
            let texture = new THREE.Texture(canvas);
            texture.needsUpdate = true;

            let material = new THREE.SpriteMaterial({map:texture});
            let textObj = new THREE.Sprite(material);
            textObj.scale.set(0.5 * 200, 0.25 * 200, 0.75 * 200);
            textObj.position.set(x,y,z);
            this.scene.add( textObj );
        },

        initText(){
            this.createSpriteText2('',-600,150,-20);
            this.createSpriteText2('12',-530,100,-190);
            this.createSpriteText2('',-610,140,-390);
            this.createSpriteText2('16',-150,100,-150);
            this.createSpriteText2('13',-300,100,-350);
            this.createSpriteText2('14',30,100,-450);
            this.createSpriteText2('15',430,100,-450);
            this.createSpriteText2('18',480,100,-250);
            this.createSpriteText2('19',480,100,-150);
            this.createSpriteText2('17',250,150,-250);
            this.createSpriteText2('4',600,100,-20);
            this.createSpriteText2('7',170,95,430);
            this.createSpriteText2('8',-120,95,430);
            this.createSpriteText2('9',-400,95,430);
            this.createSpriteText2('',-430,95,250);
            this.createSpriteText2('11',-530,95,100);
            this.createSpriteText2('',-20,-15,30);
            this.createSpriteText2('',-580,40,420);
            this.createSpriteText2('6',430,70,410);
            this.createSpriteText2('5',650,100,330);
        },


        //7.OBJ
        initObject() {
            this.createFloor();
            this.createFloor2();
            this.createFloor3();
            this.createLayout();
            this.initText();
        },


        onDocumentMouseMove(event) {
            event.preventDefault();
            this.mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
            this.mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;
        },

        render() {
            // requestAnimationFrame(this.render);
            this.renderer.render(this.scene, this.camera);
            // this.renderer.autoClear = false;

            this.raycaster.setFromCamera(this.mouse, this.camera);
            let intersects = this.raycaster.intersectObjects(this.scene.children);
            if (intersects.length > 0) {
                if (this.INTERSECTED != intersects[0].object) {
                    if (this.INTERSECTED) this.INTERSECTED.material.color.setHex(this.INTERSECTED.currentHex);
                    this.INTERSECTED = intersects[0].object;
                    this.INTERSECTED.currentHex = this.INTERSECTED.material.color.getHex();
                    this.INTERSECTED.material.color.set( 0xBED264 );
                }
            } else {
                if (this.INTERSECTED) this.INTERSECTED.material.color.set(this.INTERSECTED.currentHex);
                this.INTERSECTED = null;
            }
        },
        

        animate(){
            this.cars.forEach(car => {
                this.carMoving(car)
            })
            requestAnimationFrame( this.animate );
            this.renderer.render( this.scene, this.camera );
            TWEEN.update( );
            this.update();
            this.render();
        },

        update(){
            let delta = this.clock.getDelta();
            let moveDistance = 200 * delta;
            let rotateAngle = Math.PI / 2 * delta;
            this.controls.update();
        },
        

        //
        init(){
            this.initScene();
            this.initCamera();
            this.initRender();
            this.initEvent();
            this.initControls();
            this.initLight();
            this.initObject();
            // this.render();
            // this.objGroup123();
            this.buildbuilding();
            
            this.buildMovingCars();
            this.buildStaticCars();

        }

    },
    components: {
        leftPane,
        rightPane,
        mjleftPane,
        mjrightPane,
        peopleTable,
        peoplegateTable,
        clgateTable,
        dzwlgateTable,
        ddcgateTable,
        mjTabel,
        mokeTabel,
        houseDetailljyy
    }

}

</script>
