<template>
  <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-position="left" label-width="0px" class="demo-ruleForm login-container">
    <h3 class="title">-(admin,654321)</h3>
    <el-form-item prop="account">
      <el-input class="c" type="text" v-model="ruleForm2.account" auto-complete="off" placeholder=""></el-input>
    </el-form-item>
    <el-form-item prop="checkPass">
      <el-input class="c" type="password" v-model="ruleForm2.checkPass" auto-complete="off" placeholder="" @keyup.enter.native="handleSubmit2"></el-input>
    </el-form-item>
    <el-checkbox v-model="checked" checked class="remember"></el-checkbox>
    <el-form-item style="width:100%;">
      <el-button type="primary" style="width:100%;" @click.native.prevent="handleSubmit2" :loading="logining"></el-button>
      <!--<el-button @click.native.prevent="handleReset2"></el-button>-->
    </el-form-item>
  </el-form>
</template>

<script>
import { requestLogin } from '../api/api';
//import NProgress from 'nprogress'
export default {
    data() {
        return {
            logining: false,
            ruleForm2: {
                account: '',
                checkPass: ''
            },
            rules2: {
                account: [
                    { required: true, message: '', trigger: 'blur' }
                    //{ validator: validaePass }
                ],
                checkPass: [
                    { required: true, message: '', trigger: 'blur' }
                    //{ validator: validaePass2 }
                ]
            },
            checked: true
        };
    },
    methods: {
        handleReset2() {
            this.$refs.ruleForm2.resetFields();
        },
        handleSubmit2(ev) {
            var _this = this;
            this.$refs.ruleForm2.validate((valid) => {
                if (valid) {
                    //_this.$router.replace('/table');
                    this.logining = true;
                    //NProgress.start();
                    let loginParams = { username: this.ruleForm2.account, password: this.ruleForm2.checkPass };
                    requestLogin(loginParams).then(data => {
                        this.logining = false;
                        //NProgress.done();
                        let { msg, code, user } = data;
                        if (code !== 200) {
                            this.$message({
                                message: msg,
                                type: 'error'
                            });
                        } else {
                            sessionStorage.setItem('user', JSON.stringify(user));
                            this.$router.push({ path: '/map' });
                        }
                    });
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        }
    }
}

</script>

<style lang="scss" scoped>
  .login-container {
    /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin: 180px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
    .title {
      margin: 0px auto 40px auto;
      text-align: center;
      color: #505458;
      font-size: 20px;
    }
    .remember {
      margin: 0px 0px 35px 0px;
    }
  }
  .c{
      color: #333;
  }
</style>


