
import Layout from '@/pages/Layout'
import Map from '@/pages/map/map'
import Cjxd from '@/pages/map/cjxd'
import Ljyy from '@/pages/map/ljyy'
import Wtjy from '@/pages/map/wtjy'
import Sjw from '@/pages/map/sjw'
import Clty from '@/pages/map/clty'
import Lhmj from '@/pages/map/lhmj'
import Gzhy from '@/pages/map/gzhy'
import Lxc from '@/pages/map/lxc'
import Hjhy from '@/pages/map/hjhy'
import Cockpit from '@/pages/cockpit/cockpit'
import Login from '@/pages/Login'



let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/',
        component: Layout,
        redirect: '/map',
        children: [{
            path: '/map',
            component: Map
        }, {
            path: '/cockpit',
            component: Cockpit
        }]
    }
    
];

export default routes;



